* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.head {
  font-size: 25px;
  width: 100%;
  background-image: linear-gradient(rgb(5, 2, 31), rgb(10, 7, 39));
  color: white;
  justify-content: center;
  display: flex;
  padding: 0.2rem;
}

.section {
  width: 100%;
  height: calc(50vh - 20px);
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  gap: 0.5rem;
  padding: 10px;
}

.container {
  width: 100%;
  height: 100%;
  background-color: rgb(5, 2, 31);
  border-radius: 4px;
}

.container-title {
  color: aliceblue;
  padding: 0.2rem 0.5rem;
  font-size: large;
}

.container .code {
  width: 100%;
  height: 100%;
  background-color: rgb(42, 49, 61);
  border-radius: 4px;
  overflow: hidden;
}
